import React from "react"
import { useStaticQuery, graphql } from "gatsby";
import Img from "gatsby-image";

import Layout from "../components/layout"
import SEO from "../components/seo"

// Components
import AboutLinks from "../components/AboutLinks/AboutLinks.component";
import TonisityProduct from "../components/TonisityProduct/TonisityProduct.component";

const About = () => {

  const data = useStaticQuery(graphql`
      query {
        aboutFolder: allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(about)/"}}, limit: 2) {
          edges {
            node {
              frontmatter {
                title
                description
                productBenefits
                productPositive01
                productPositive02
                productPositive03
                productPositive04
                productBody
              }
              id
            }
          }
        }
        aboutPage: allMarkdownRemark(filter: {frontmatter: {title: {eq: "About"}}}) {
          edges {
            node {
              frontmatter {
                headerImage {
                  childImageSharp {
                    fluid {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
      }
    `)

  return (
    <Layout>
      <SEO title="About" />
      {
        data.aboutPage.edges.map(({node}) => (
          <Img fluid={ node.frontmatter.headerImage.childImageSharp.fluid } />
        ))
      }
      <AboutLinks />
      {
        data.aboutFolder.edges.map(({node}) => (
          <TonisityProduct 
            productTitle={node.frontmatter.title} 
            productDescription={node.frontmatter.description}
            productBenefits={node.frontmatter.productBenefits}
            productPositives01={node.frontmatter.productPositive01}
            productPositives02={node.frontmatter.productPositive02}
            productPositives03={node.frontmatter.productPositive03}
            productPositives04={node.frontmatter.productPositive04}
            productBody={node.frontmatter.productBody}
            productId={node.id}
          />
        ))
      }
    </Layout>
  )
}

export default About
